import React, { useState, useEffect } from "react";
import "../auth.scss";

import ResetPassword from "../resetPwd/resetPwd";
import Input from "../../../components/Input/Input";
import Button from "../../../components/Button/Button";
import Dialog from "../../../components/Dialog/Dialog";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { loginRequest } from "../../../data/slices/auth";
import { RootState } from "../../../store";

const Login = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const { loading, error, isAuthenticated } = useSelector(
    (state: RootState) => state.auth
  );

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  // ‚úÖ Navigate ONLY when login success
  useEffect(() => {
    if (isAuthenticated) {
      navigate("/category/women");
    }
  }, [isAuthenticated, navigate]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    // ‚úÖ Validation
    if (!email || !password) {
      alert("Please enter email and password");
      return;
    }

    dispatch(
      loginRequest({
        email,
        password,
      })
    );
  };

  const resetPassword = (email: string): void => {
    console.log("Reset password email:", email);
  };

  const navigateTo = () => {
    navigate("/signup");
  };

  return (
    <>
      <section className="auth-page">
        <div className="auth-card">
          <form onSubmit={handleSubmit}>
            {/* Email */}
            <div className="form-group">
              <Input
                type="email"
                placeholder="Email"
                value={email}
                onChange={(e: any) => setEmail(e.target.value)}
              />
            </div>

            {/* Password */}
            <div className="form-group">
              <Input
                type="password"
                placeholder="Password"
                togglePassword
                value={password}
                onChange={(e: any) => setPassword(e.target.value)}
              />
            </div>

            {/* Error */}
            {error && <div className="error-text">{error}</div>}

            {/* Actions */}
            <div className="login-act-btns mb-4">
              <Button type="submit" disabled={loading}>
                {loading ? "Logging in..." : "Login"}
              </Button>

              <button
                type="button"
                className="link-btn"
                data-bs-toggle="modal"
                data-bs-target="#resetModal"
              >
                Forgot password?
              </button>
            </div>

            {/* Signup */}
            <Button
              type="button"
              variant="outline"
              className="signup-btn"
              onClick={navigateTo}
            >
              Create your Clothwyn account
            </Button>
          </form>
        </div>
      </section>

      {/* Reset Password Modal */}
      <Dialog id="resetModal" title="Reset Password">
        <ResetPassword onReset={resetPassword} />
      </Dialog>
    </>
  );
};

export default Login;
